@import url("https://fonts.googleapis.com/css2?family=Short+Stack:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-short-stack: "Short Stack";

  /* font sizes */
  --font-size-xl: 20px;
  --font-size-5xl: 24px;
  --font-size-13xl: 32px;

  /* Colors */
  --color-white: #fff;
  --color-black: #000;
  --color-mediumvioletred-100: #cd3f97;
  --color-mediumvioletred-200: #aa1070;
  --color-hotpink: #ed76c0;
  --color-lavender: #f8ebfd;
  --color-lightseagreen: #25a3a1;
  --color-paleturquoise: #a4fdfb;
  --color-gray: #1e242f;
  --color-lightgray: #ccc;

  /* Border radiuses */
  --br-11xs: 2px;
  --br-5xs: 8px;
  --br-8xs: 5px;
  --br-3xs: 10px;
}
